import React, { Component } from 'react';
import * as firebase from 'firebase';


class details extends Component {
    constructor(props){
        super(props);
        this.state ={ 
          data: null
        }
    }


    componentDidMount(){
        var id = this.props.match.params.id;
        firebase.database().ref('/portfolio/siteweb/'+id).on('value', (snapshot) => {
            const userObj = snapshot.val();
            this.setState({
                data:userObj
            })
        });
    }


  render() {
    if(this.state.data === null){
        return(
            <section class="portfolio detail-portfolio">
                <div className="header text-center"> 
                    <h2>Site Web </h2>
                    <div class="separator align-center">
                        <span></span>
                    </div>
                </div>
                <br></br>
                <center><p>Chargement...</p></center>
            </section>
        )
    }
    return (
      <section class="portfolio detail-portfolio">
            <div className="header text-center"> 
                <h2>Site Web </h2>
                <div class="separator align-center">
                    <span></span>
                </div>
            </div>
            <br></br>
            <div class="container fadeIn wow">
                <div className="row blc-1">
                    <div className="col-md-3">
                        <img src={this.state.data.image} className="img-fluid"/>
                    </div>

                    <div className="col-md-9">
                        <div>
                            <p style={{fontWeight:700}}>{this.state.data.titre}</p>
                            <p>{this.state.data.description}</p>
                            <p><a href={this.state.data.lien} target="_blank">{this.state.data.lien}</a></p>
                        </div>
                    </div>
                </div>
                <br></br>
                <div>
                <hr></hr><br></br>
                    <h4>Captures d’écran</h4> 
                    <div className="row blc-2">
                        <div className="col-6">
                            <img src={this.state.data.image} className="img-fluid"/>
                        </div>
                        <div className="col-6">
                            <img src={this.state.data.image1} className="img-fluid"/>
                        </div>
                        <div className="col-6">
                            <img src={this.state.data.image2} className="img-fluid"/>
                        </div>
                    </div>
                </div>
                <br></br>
                <hr></hr>
                <br></br>
                <div className="">
                    <h4>Technologies: <span>{this.state.data.techno}</span></h4>
                </div>
                <hr></hr>
                <br></br>

                <div className="">
                    <h4>Entreprise: <a href={this.state.data.lien_entreprise} target="_blank">{this.state.data.entreprise}</a></h4>
                </div>
                <br></br>
                <br></br>
                <br></br>
          </div>
        </section>
    );
  }
}

export default details;
