import React, { Component } from 'react';
import LandingPage from './Landingpage';
import Dashboard from './Dashboard';
import SiteWeb from './SiteWeb';
import Mobiledev from './Mobiledev';
import All from './All';

class Portfolio extends Component {
  constructor(props) {
    super();
      this.state = {
        portfolio: ''
      }
  }


  handleClickSiteWeb(value) {
    this.setState({
      portfolio: value
    })
  }

  handleClickLp(value) {
    this.setState({
      portfolio: value
    })
  }

  handleClickDashboard(value) {
    this.setState({
      portfolio: value
    })
  }


  handleClickMobileDev(value) {
    this.setState({
      portfolio: value
    })
  }

  handleClickAll(value) {
    this.setState({
      portfolio: value
    })
  }

  

  renderPortfolio() {
    if(this.state.portfolio === 'lp') {
      return <LandingPage/>
    } 
    if(this.state.portfolio === 'site') {
      return <SiteWeb/>
    } 
    if(this.state.portfolio === 'dashboard') {
      return <Dashboard/>;
    } 
    if(this.state.portfolio === 'mobiledev') {
      return <Mobiledev/>;
    } 
    if(this.state.portfolio === 'all') {
      return <div>
                <All/>
              </div>;
            } 

    else {
      return <div>
                <All/>
              </div>;
      }
  }


  render() {
    return (
        <section class="portfolio text-center">
            <div className="header"> 
                <h2>Portfolio</h2>
                <div class="separator align-center">
                    <span></span>
                </div>
            </div>
            <br></br><br></br>
            <div class="sect-container fadeIn wow">
                <div class="button-group filter-portfolio">
                    <a class="button hvr-grow" onClick={()=>this.handleClickAll('all')}>ALL</a>
                    <a class="button hvr-grow" onClick={()=>this.handleClickLp('lp')}>Landing page</a>
                    <a class="button hvr-grow" onClick={()=>this.handleClickSiteWeb('site')}>Site Web</a>
                    <a class="button hvr-grow" onClick={()=>this.handleClickDashboard('dashboard')}>Dashboard</a>
                    <a class="button hvr-grow" onClick={()=>this.handleClickMobileDev('mobiledev')}>Mobile dev</a>
                </div>
                <br></br><br></br>

                {this.renderPortfolio()}
            </div>
        </section>
    );
  }
}

export default Portfolio;
