import React, { Component, Fragment } from "react";
import { Link } from 'react-router-dom'
import * as firebase from 'firebase';


class AllScreen extends Component {
    constructor(props){
        super(props);
        this.state ={ 
          isLoading: true,
          lp: [],
          siteweb:[],
          dashboard:[]
        }
      }

    componentDidMount(){
        firebase.database().ref('/portfolio/lp').on('value', (snapshot) => {
            const userObj = snapshot.val();
            this.setState({
              lp: userObj
            })

            console.log('result', userObj)
          });
        
          firebase.database().ref('/portfolio/siteweb').on('value', (snapshot) => {
            const userObj = snapshot.val();
            this.setState({
                siteweb: userObj
            })
          });

          firebase.database().ref('/portfolio/dashboard').on('value', (snapshot) => {
            const userObj = snapshot.val();
            this.setState({
                dashboard: userObj
            })
          });
      }

      render(){
        return(
            <div className="col-md-8 offset-md-2">
                    <div class="portfolio-content">
                        <div className="row">
                            {this.state.lp.map((item, i) => {
                                return (
                                <Fragment key={i}>
                                    <div className="col-md-4 fadeIn wow hvr-grow">
                                    <Link to={`/landing-page/${i}`} className="nav-link">
                                        <div class="portfolio-item item-lp" >
                                            <div class="portfolio-caption">
                                                <a class="modal-image">
                                                    <img src={item.image} className="img-fluid"/>
                                                    <div class="overlay">
                                                        <div class="info-content">
                                                            <h4>{item.titre}</h4>
                                                        </div>
                                                    </div>
                                                    </a>
                                            </div>
                                        </div>
                                        </Link>
                                    </div>
                                    </Fragment>
                                    );
                                })}

                                {this.state.siteweb.map((item, i) => {
                                return (
                                <Fragment key={i}>
                                    <div className="col-md-4 fadeIn wow hvr-grow">
                                        <div class="portfolio-item item-lp" >
                                            <div class="portfolio-caption">
                                                <a class="modal-image">
                                                    <img src={item.image} className="img-fluid"/>
                                                    <div class="overlay">
                                                        <div class="info-content">
                                                            <h4>{item.titre}</h4>
                                                        </div>
                                                    </div>
                                                    </a>
                                            </div>
                                        </div>
                                    </div>
                                    </Fragment>
                                    );
                                })}

                            {this.state.dashboard.map((item, i) => {
                                return (
                                <Fragment key={i}>
                                    <div className="col-md-4 fadeIn wow hvr-grow">
                                        <div class="portfolio-item item-lp" >
                                            <div class="portfolio-caption">
                                                <a class="modal-image">
                                                    <img src={item.image} className="img-fluid"/>
                                                    <div class="overlay">
                                                        <div class="info-content">
                                                            <h4>{item.titre}</h4>
                                                        </div>
                                                    </div>
                                                    </a>
                                            </div>
                                        </div>
                                    </div>
                                    </Fragment>
                                    );
                                })}
                    </div>
                    </div>
                </div>
            );
    }
}

export default AllScreen;
